import React from 'react';
import ReactDOM from 'react-dom';
import { CircleSlider } from 'react-circle-slider';
import './index.css';

class App extends React.Component {
	constructor(state) {
		super(state);
		this.state = {
			gradtype: false,
			bgcolor: [
				{ color: '#FF0BF2', stop: '0' },
				{ color: '#E7F62A', stop: '30' }
			],
			deg: '45'
		};
	}

	setBackground = () => {
		let bgcolorstr = '';
		if (this.state.gradtype) {
			bgcolorstr = `radial-gradient(circle`;			
		} else {
			bgcolorstr = `linear-gradient(` + this.state.deg + `deg`;		
		}		
		for (let i=0; i<this.state.bgcolor.length; i++) {
			bgcolorstr += ', ' + this.state.bgcolor[i].color + ' ' + this.state.bgcolor[i].stop + '%' 
		}
		bgcolorstr += ')';
		console.log(bgcolorstr);
		document.body.style.background = bgcolorstr;			
		document.body.style.backgroundRepeat = 'no-repeat';
		document.body.style.backgroundAttachment = 'fixed'
	}

	setToRadial = () => {
		this.setState({ gradtype: true });
		document.getElementById('degSetter').style.display = 'none';
	}

	setToLinear = () => {
		this.setState({ gradtype: false });
		document.getElementById('degSetter').style.display = 'initial';
	}

	addNewColorCard = () => {
		const bgcolor = this.state.bgcolor.slice();
		bgcolor.push({ color: '#F8F7F7', stop: '80' });
		this.setState({bgcolor: bgcolor})
	}

	onColorChange = (event) => {
		const bgcolor = this.state.bgcolor.slice();
		bgcolor[event.target.id].color = event.target.value;
		this.setState({bgcolor: bgcolor})			
	}

	onStopPointChange = (event) => {
		console.log('event');
		const bgcolor = this.state.bgcolor.slice();
		bgcolor[event.target.id].stop = event.target.value;
		this.setState({bgcolor: bgcolor})
	}

	onRemoveCard = (event) => {
		let bgcolor = this.state.bgcolor.slice();
		let bgcolornew = bgcolor.splice(event.target.parentElement.className, 1);
		this.setState({bgcolor: bgcolor})
	}

	onDegChange = (event) => {
		this.setState({ deg: event })
	}

	render() {		
		this.setBackground();		
		return(
			<div>
				<h1>Background Generator</h1>
				<div className='controlbar'>
					<Controll gradTypeR={this.setToRadial} gradTypeL={this.setToLinear} />
					<div className='degSetter' id='degSetter'>
						<CircleSlider 
							onChange={this.onDegChange} 
							value={this.state.deg} 
							size={150} max='361' 
							shadow={false}
							knobRadius={10}
							knobColor="#ff5722" 
							showTooltip={true} 
							progressColor="#FDB11B"
							progressWidth={10}
							circleWidth={10}
						 />
					</div>
					<ColorCardList 
						bgcolor={this.state.bgcolor} 
						colorChange={this.onColorChange} 
						stopPointChange={this.onStopPointChange} 
						removeCard={this.onRemoveCard}
					 />
					<button type='button' onClick={this.addNewColorCard}><h3>+</h3></button>
				</div>
			</div>
		);
	}
}

class ColorCardList extends React.Component {
	render() {
		return (
			<div>
				{
					this.props.bgcolor.map((color, i) => {
						return (
							<ColorCard 
								key={i}
								num={i}
								color={this.props.bgcolor[i].color} 
								stop={this.props.bgcolor[i].stop}
								colorChange={this.props.colorChange} 
								stopPointChange={this.props.stopPointChange}
								removeCard={this.props.removeCard}
							/>
						);
					})
				}
			</div>
		);
	}
}

class ColorCard extends React.Component {
	render() {
		return(
			<div className={this.props.num} >
				<input 
					className="color" 
					id={this.props.num} 
					type="color"
					name={'color' + this.props.num}
					defaultValue={this.props.color} 
					onChange={this.props.colorChange} 
				/>
				<input 
					className='stopbar' 
					id={this.props.num}
					type='range' 
					min='0' 
					max='100' 
					defaultValue={this.props.stop} 
					onChange={this.props.stopPointChange} 
				/>
				<button type='button' onClick={this.props.removeCard}>-</button>
			</div>
		);
	}
}

class Controll extends React.Component {
	render() {
		return(
			<div>
				<button type='button' onClick={this.props.gradTypeL} >Linear Gradient</button>
				<button type='button' onClick={this.props.gradTypeR}>Radial Gradient</button>
			</div>
		);
	}
}


ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);
